import React, {useState, useContext} from 'react'
import { MyContext } from '../../../../pages/Make/MakePageV2'
import EditAnimation from '../tools/EditAnimation'
import { detailSectionTemplateList } from './InnerTemplates'
import TemplateChoose from '../tools/TemplateChoose'
import {EditRadioContainer} from '../tools/RadioCustom'
import produce from 'immer';
import {EditColorContainer} from '../tools/EditColor'
import CheckBoxContainer from '../tools/CheckBoxContainer'
import ImageAddEdit from '../tools/ImageAddEdit'

const paddingOptions = [
    { label: '', value: 0 },
    { label: '', value: 5 },
    { label: '', value: 10 },
    { label: '', value: 20 },
]
const imageBorderOptions = [
    { label: '', value: 50 },
    { label: '', value: 7 },
    { label: '', value: 0 },
]
const imageSizeOptions = [
    { label: '', value: 150 },
    { label: '', value: 200 },
    { label: '', value: 300 },
]

const imageOptions = [
    { label:'', value:'video'},
    { label:'', value:'image'},
    { label:'', value:'mockup'},
    { label:'', value:'slide'},
]

function EditHeroSection({content, category}) {
    const {state, action} = useContext(MyContext) //ContextAPI state action .
    const [selectOpen, setSelectOpen] = useState(false)

    const changeImageOption = e => {
        action.setContents(produce(state.contents, draft => {
            draft[state.secNum].image.type = e;
        }));
    }

    //  2  
    const onChangeBackgroundImage = e => {
        let newContents = JSON.parse(JSON.stringify(state.contents))
        const {target:{files},} = e;
        const oneFile = files[0];
        const reader = new FileReader();
        reader.onloadend = (finishedEvent) => { //     .
            const {currentTarget:{result}} = finishedEvent;
            // newContents = state.contents.map((item, index) => index === state.secNum ? {...item, image: {...item.image, attachment : result}} : item)
            newContents[state.secNum].backgroundImage.attachment = result;
        }
        reader.readAsDataURL(oneFile);
        action.setContents(newContents);
    }

    const returnImageOrVideoAdd = () => {
        switch(content.image.type){
            case 'image':
                return(
                    <div className="edit-element">
                        <div className="edit-element__one">
                            <div className="edit-element__left"> </div>
                            <div className="edit-element__right">
                            </div>
                        </div>
                    </div>
                )
            
            case 'video':
                return(
                    <div className="edit-element">
                        <div className="edit-element__one">
                            <div className="edit-element__left"> </div>
                            <div className="edit-element__right">
                            </div>
                        </div>
                    </div>

                )
            default:
                return(
                    <div></div>
                )
        }
    }

    const returnTable = () => {
        switch(category){
            case 0:
                // case 0  
                return(
                    <>
                    <div className="edit-element">
                        <div className="edit-element__one">
                              
                        </div>
                    </div>
                    <EditColorContainer text={" "} value={content.backgroundColor} func={e => action.setContents(produce(state.contents, draft => {
                            draft[state.secNum].backgroundColor = e
                        }))} />
                    <div className="edit-element">
                        <div className="edit-element__one">
                            <div className="edit-element__left">  </div>
                            <div className="edit-element__right">
                                <input onChange={(e) => action.setContents(produce(state.contents, draft => {
                                    draft[state.secNum].backgroundOpacity = e.currentTarget.value
                                }))} value={content.backgroundOpacity} type="number" />
                            </div>
                        </div>
                    </div>
                    <CheckBoxContainer text="  " value={content.backgroundImage.use} func={ () => action.setContents(produce(state.contents, draft => {
                        draft[state.secNum].backgroundImage.use = !content.backgroundImage.use;
                    }))} />
                    {
                        content.backgroundImage.use && 
                            <ImageAddEdit text="  " value={content.backgroundImage.attachment} func={e => onChangeBackgroundImage(e)} />
                    }
                    <EditRadioContainer text=" " options={paddingOptions} value={content.paddingSize} func={e => action.setContents(produce(state.contents, draft => {
                            draft[state.secNum].paddingSize = e;
                        }))} />
                    <EditRadioContainer text=" " options={imageBorderOptions} value={content.image.border} func={e =>  action.setContents(produce(state.contents, draft => {
                                        draft[state.secNum].image.border = e;
                                    }))} />
                    <EditRadioContainer text=" " options={imageSizeOptions} value={content.image.size} func={e =>  action.setContents(produce(state.contents, draft => {
                                        draft[state.secNum].image.size = e;
                                    }))} />
                    <EditRadioContainer text=" " options={imageOptions} value={content.image.type} func={e => changeImageOption(e)} />
                    
                    {
                        returnImageOrVideoAdd()
                    }
                    
                    <CheckBoxContainer text=" 1 " value={content.button.first} func={ () => action.setContents(produce(state.contents, draft => {
                        draft[state.secNum].button.first = !content.button.first;
                    }))} />
                    <CheckBoxContainer text=" 2 " value={content.button.second} func={ () => action.setContents(produce(state.contents, draft => {
                        draft[state.secNum].button.second = !content.button.second;
                    }))} />
                    </>
                )

            case 1:
                // case 1  
                return(
                    <>
                        <TemplateChoose content={content} title="" list={detailSectionTemplateList} />
                    </>
                )

            default:
                <></>
        }
    }

    return(
        <>
            {returnTable()}
        </>
    )
}

export default EditHeroSection
