import React, {useContext} from 'react'
import { MyContext } from '../../../../pages/Make/MakePageV2'
import { priceSectionTemplateList } from './InnerTemplates'
import TemplateChoose from '../tools/TemplateChoose'

function EditPriceSection({content, category}) {
    const {state, action} = useContext(MyContext) //ContextAPI state action .

    const returnTable = () => {
        switch(category){
            case 0:
                // case 0  
                return(
                    <div className="section-make__inner-container">
                        {state.secNum}   .
                    </div>
                )

            case 1:
                // case 1  
                return(
                    <>
                        <TemplateChoose content={content} title="" list={priceSectionTemplateList} />
                    </>
                )

            default:
                <></>
        }
    }


    return(
        <>
            {returnTable()}
        </>
    )
}

export default EditPriceSection
